@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@400;500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Google font import */

.App {
    width: 100vw;
    overflow: hidden;
    font-family: 'Quicksand', sans-serif;
}
/* main header */
#home {
    font-family: 'Quicksand', sans-serif;
    background-image: url(/static/media/sky-bg.b7b4b195.jpg);
    background-size: cover;
    background-position: center center;
    background-repeat: no-repeat;
}

header {
    position: relative;
    height: 100vh;
    width: 100%;
    text-align: center;
    overflow: hidden;
}

header:before {
    content: '';
    display: inline-block;
    vertical-align: middle;
    height: 100%;
}

header .banner {
    display: inline-block;
    vertical-align: middle;
    margin: 0 auto;
    width: 85%;
    padding-bottom: 30px;
    text-align: center;
}

/* text styling */
header .banner-text { width: 100%; }

header .banner-text h1 {
    color: #fff;
    letter-spacing: 2px;
    margin: 0 auto 18px auto;
    font-weight: 500;
}

header .banner-text h2 {
    color: #fff;
    margin: 0 auto;
    width: 70%;
    letter-spacing: 2px;
    font-weight: 500;
}

header .banner-text hr {
    width: 60%;
    margin: 18px auto 24px auto;
    border-color: #2F2D2E;
    border-color: rgba(150, 150, 150, .1);
}

#my-photo {
    margin-top: 2rem;
    border-radius: 50%;
    width: 150px;
}
 
/* header social links */
header .social {
    margin: 24px 0;
    padding: 0;
    font-size: 30px;
}

header .social ul {
    display: inline-block;
    margin: 0 15px;
    padding: 0;
}

header .social a { 
    color: #fff;
    font-size: 42px;
}

header .social a:hover {
    color: #00c2cb;
}
 
/* scroll down arrow */
header .scrolldown a {
    position: absolute;
    bottom: 30px;
    left: 50%;
    margin-left: -27px;
    color: #fff;
    display: block;
    height: 42px;
    width: 42px;
    font-size: 42px;
    line-height: 42px;
    border-radius: 100%;
    transition: all .3s ease-in-out;
}

header .scrolldown a:hover {
    color: #00c2cb;
}
 
/* navigation */
#nav-wrap ul, #nav-wrap li, #nav-wrap a {
    margin: 0;
    padding: 0;
    border: none;
    outline: none;
}
 
/* nav-wrap */
#nav-wrap {
    width: 100%;
    text-transform: uppercase;
    letter-spacing: 2.5px;
    margin: 0 auto;
    z-index: 100;
    position: fixed;
    left: 0;
    top: 0;
}

.opaque { background-color: #333; }
 
/* hide toggle button */
#nav-wrap > a.mobile-btn { display: none; }
 
ul#nav {
    min-height: 48px;
    width: auto;
 
    /* center align the menu */
    text-align: center;
}

ul#nav li {
    position: relative;
    list-style: none;
    height: 48px;
    display: inline-block;
}
 
/* Links */
ul#nav li a {
    display: inline-block;
    padding: 8px 13px;
    line-height: 32px;
    text-decoration: none;
    text-align: left;
    color: #fff;
    transition: color .2s ease-in-out;
}
 
ul#nav li a:active { background-color: transparent !important; }
ul#nav li.current a { color: #00c2cb; }

/* Footer Styling */
footer {
    font-family: 'Quicksand', sans-serif;
    width: 100%;
    height: 550px;
    background-color: #333333;
    color: #ffffff;
    text-align: center;
    position: relative;
}

#footer-logo {
    position: absolute;
    top: -35px;
    left: 47%;
}

/* Contact Section Styling */
#contact-section {
    padding-top: 90px;
    width: 600px;
    margin: auto;
}

#contact-form {
    width: 100%;
}

.heading-style {
    text-align: left;
    font-weight: 800;
}

.contact-text {
    color: white;
    margin-top: 2rem;
    margin-bottom: 3rem;
    text-align: left;
}

.form-row {
    display: flex;
    justify-content: space-between;
    margin-bottom: 1rem;
}

.input-field {
    padding: 10px;
    width: 275px !important;
    border-radius: 5px;
    outline: none;
    border: none;
    font-family: 'Quicksand', sans-serif;
    font-weight: 500;
}

#u-msg {
    padding: 10px;
    width: 580px;
    border-radius: 5px;
    outline: none;
    border: none;
    font-family: 'Quicksand', sans-serif;
    font-weight: 500;
}

#submitBtn {
    padding: 10px;
    width: 100px;
    border-radius: 5px;
    outline: none;
    border: none;
    font-family: 'Quicksand', sans-serif;
    font-weight: 500;
    text-transform: uppercase;
}

#submitBtn:hover {
    background-color: #00c2cb;
    color: #ffffff;
}

#stop-submit {
    color: red;
    margin-top: 1rem;
    margin-left: 1rem;
}

#footer-text {
    display: block;
    padding-top: 40px;
}
.modal-title {
    color: #ffffff;
}

.modal-content {
    background-color: #00c2cb;
    color: #ffffff;
}
#about {
    font-family: 'Quicksand', sans-serif;
    background-image: url(/static/media/grey-pattern.856f31d9.jpg);
    background-size: cover;
    background-position: center center;
    background-repeat: no-repeat;
    padding: 5% 0;
}

.title {
    text-align: center;
    color: #006166;
    padding: 3rem;
}

#about-me {
    margin-left: 25%;
    margin-right: 25%;
    font-weight: 400;
}

#resumeBtn {
    width: 8rem;
    padding: 10px;
    color: #006166;
    font-weight: 500;
    background-color: transparent;
    border: 2px solid #006166;
    border-radius: 30px;
    text-decoration: none;
    text-align: center;
}

#resumeBtn:hover {
    background-color: #006166;
    color: white;
    outline: none;
}

.icon-container {
    display: flex;
    flex-direction: row;
    margin-bottom: 3rem;
    margin-left: 26%;
    margin-right: 26%;
}

.icon-parent {
    text-align: center;
    padding: 2rem;
}

.icon-box {
    display: inline-block;
    height: 80px;
    width: 80px;
    line-height: 80px;
    background-color: #00c2cb;
    color: white;
    border-radius: 50%;
    text-align: center;
    margin-bottom: 10px;
}

.myIcon {
    font-size: 30px;
    display: inline-block;
    vertical-align: middle;
    line-height: normal;
}

.icon-title {
    color: #006166;
    font-weight: 900;
    text-transform: uppercase;
}

/* Slider */
.tt-cont {
    padding-bottom: 7rem;
    margin-left: 20%;
    margin-right: 20%;
}
#projects {
    font-family: 'Quicksand', sans-serif;
	height: 100%;
	padding: 5% 0;
}

.section-title {
	text-align: center;
	margin-bottom: 3rem;
	color: #006166;
}

.card-cont {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
    margin-left: 15%;
}

.img-cont {
	background-color: rgba(77, 77, 77, 0.7);
	padding: 0;
	overflow: hidden;
	max-width: 500px;
    min-height: 285px;
	margin: 5px;
	position: relative;
}

.img-bar {
	position: absolute;
	background-color: #00c2cb;
	width: 100vw;
	height: 70px;
	bottom: 0;
	z-index: 1;
	opacity: 0;
	transform: translate(0px, 70px);
	-webkit-transform: translate(0px, 70px);
	-moz-transform: translate(0px, 70px);
	-o-transform: translate(0px, 70px);
	-ms-transform: translate(0px, 70px);
	transition: all 0.2s ease-in-out;
}

.img-bar a {
	font-size: 40px;
	color: #fff;
	position: relative;
	width: 100%;
	top: 13px;
	margin-left: 16%;
}

.img-cont article {
	white-space: pre-wrap;
	position: absolute;
	padding: 7%;
	bottom: 0;
	z-index: 1;
	transition: all 0.5s ease;
}

.img-cont h2 {
	text-align: left;
	color: #fff;
	font-weight: 800;
	font-size: 25px;
	border-bottom: #fff solid 1px;
}

.img-cont h4 {
	font-weight: 300;
	color: #fff;
	font-size: 16px;
}

.img-cont a {
	text-align: center;
    color: #fff;
}

.img-cont img {
	z-index: 0;
	position: relative;
	width: 100%;
	top: 0;
	left: 0;
	opacity: 0.4;
	transition: all 4s ease;
}

/* Card Hovers */
.img-cont a:hover {
	color: #00c2cb;
	text-decoration: none;
}

.img-cont:hover img {
	opacity: 0.1;
	transform: scale(1.5);
}

.img-cont:hover article {
	transform: translate(2px, -69px);
	-webkit-transform: translate(2px, -69px);
	-moz-transform: translate(2px, -69px);
	-o-transform: translate(2px, -69px);
	-ms-transform: translate(2px, -69px);
}

.img-cont:hover .img-bar {
	transform: translate(0px, 0px);
	-webkit-transform: translate(0px, 0px);
	-moz-transform: translate(0px, 0px);
	-o-transform: translate(0px, 0px);
	-ms-transform: translate(0px, 0px);
	opacity: 1;
}

.img-bar a:hover {
    color: black;
}
